import axios from 'axios'

// 获取左边栏
export const getLeft = () => {
  return axios.get(`http://127.0.0.1:8000/alarm_rules/`)
}

// 获取报警类型下的规则详情
export const getDetail = (id) => {
  return axios.get(`http://127.0.0.1:8000/alarm_rules/${id}`)
};

// 获取用户
export const getUser = () => {
  return axios.get(`http://127.0.0.1:8000/users`)
};

// 获取游戏
export const getGame = () => {
  return axios.get(`http://127.0.0.1:8000/apps`)
};

// 新增规则
export const addRule = params => {
  return axios.post(`http://127.0.0.1:8000/add_rule`, params)
};

// 删除规则
export const deleteRule = params => {
  return axios.post(`http://127.0.0.1:8000/delete_rule`, params)
};



